"use client";

import { useParams } from "next/navigation";

export default function SubTaskPage() {
  const { id, subtaskid } = useParams();

  console.log({ id, subtaskid }); // prints the URL params

  return (
    <div className="p-4">
      <h1 className="text-xl font-bold">Subtask Details</h1>
      <p><strong>Task ID:</strong> {id}</p>
      <p><strong>Subtask ID:</strong> {subtaskid}</p>
    </div>
  );
}
